	module.exports = {"data":{"markdownRemark":{"html":"<p>早朝、私はドイツ語で書かれた「中国を見る第3の目」を読んでいます。それは中国の問題の専門家です。私はいつもドイツ語ではない、あるいは中国語ではないと感じています。しかし、この本の分析では、中国の多くの問題を分析しているが、西側の視点では基本的にいくつかの問題点がある。 \" 2つの可能性があります</p>\n<p>それぞれの目の半分を取る。中国社会の将来の最終的な分析では、著者は言った：</p>\n<p>「改革の社会は非常に脆弱であり、旧制度は機能しにくいほど難しく、新制度の安定した社会機能はまだ完全には形成されていない。 1億人の巨大な社会システムを持つ新しい情報が流入し、その受け入れプロセスを完了し、再編成を達成するには1年以上かかりますが、その距離はシステムを実際に分裂させ、不平等にします。</p>\n<p>実際、それ以上に。しかし、これらも非常に重要です。近代化の過程で、我々は高い関心を払うべきではありません。問題は、開発において、客観的な問題に注意を払い、主観的な問題に注意を払うことは矛盾であり、多くの場合、主観的な矛盾に注意を払うことが重要です。主観的な矛盾が近代化プロセスに影響を与えない場合にのみ、集中力によって客観的な矛盾を克服することができる。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0112.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0112.md","frontmatter":{"title":"6月12日 日曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0112.md absPath of file >>> MarkdownRemark"}}

